.maindiv{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 60vh;
}

h3{
    padding: 20px;
    margin-left: 100px !important;
    font-size: 40px;
    font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: #504b47;
}

.container{
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
}

.part1{
    width: 35vw;
}
.part2{
    display: flex;
    width: 65vw;
    background-color: #f1f1f1;
}

.part2_left{
width: 35%;
}

.unit_name{
    margin-top: 40px;
    margin-left: 20px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 35px;
}

.part2_left div{
    margin-left: 20px;
    margin-top: 45px;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.part2_right{
    margin-top: 45px;
    width: 65%;
    display: flex;
    justify-content: space-around;
}

.right_left_bottom{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.bt{
    background-color: black;
    color: white;
    height: fit-content;
    padding: 15px;
}

.delete{
    background-color: rgb(255, 95, 95);
    color: white;
    height: fit-content;
    padding: 15px;
}

button{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 15px;
    border-radius: 7px;
    border: none;
}

button:hover{
    transform: scale(1.1);
    -webkit-transition: transform .5s ease-in-out;
}
.part1 > div{
    margin-left: 50px;
    margin-top: 25px;
}
